import React from "react";
import {Form, FormControl, Nav, Navbar, NavDropdown} from "react-bootstrap";

export const NavigationNavbar = () => {
  return (
    <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
      <Navbar.Brand href="#home">Никита Филонов</Navbar.Brand>
      <Navbar.Toggle aria-controls="responsive-navbar-nav"/>
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="mr-auto"/>
        <Nav>
          <Form inline>
            <FormControl type="text" placeholder="Поиск" className="mr-sm-2"/>
          </Form>
          <Nav.Link eventKey={1} href="/">Главная</Nav.Link>
          <Nav.Link eventKey={2} href="/about">Обзор</Nav.Link>
          <Nav.Link eventKey={3} href="/education">Образование</Nav.Link>
          <Nav.Link eventKey={3} href="/work">Опыт работы</Nav.Link>
          <Nav.Link eventKey={3} href="/projects">Проекты</Nav.Link>
          <NavDropdown title="Язык" id="collasible-nav-dropdown">
            <NavDropdown.Item href="#action/3.1">Русский</NavDropdown.Item>
            <NavDropdown.Item href="#action/3.2">English</NavDropdown.Item>
          </NavDropdown>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  )
}
